import React, { useState } from 'react';
import Info from './Info';

const HookHome = () => {
	const [visible, setVisible] = useState(false);

	return (
		<div className="wrap">
			<h1 className="chapter__main">Hooks   .</h1>

			<button
				onClick={() => {
					setVisible(!visible);
				}}
			>
				{visible ? ' ' : ''}
			</button>
			<Info />
		</div>
	);
};

export default HookHome;
