import React, { useState } from 'react';

const Inputs = () => {
	const [form, setForm] = useState({
		username: '',
		message: '',
	});
	const { username, message } = form;
	const onChange = (e) => {
		const nextForm = {
			...form, //  form     
			[e.target.name]: e.target.value, //    
		};
		setForm(nextForm);
	};

	const onClick = () => {
		alert(username + ' : ' + message);
		setForm({
			username: '',
			message: '',
		});
	};

	const onKeyPress = (e) => {
		if (e.key === 'Enter') {
			onClick();
		}
	};

	return (
		<div className="wrap">
			<h1> </h1>

			<input
				type="text"
				name="username"
				placeholder="  "
				value={username}
				onChange={onChange}
			/>

			<input
				type="text"
				name="message"
				placeholder="  "
				value={message}
				onChange={onChange}
				onKeyPress={onKeyPress}
			/>

			<button onClick={onClick}></button>
		</div>
	);
};

export default Inputs;
